.modal__main_container {
	@apply flex justify-center items-center py-0 px-[20px];
}

.modal__movable {
	@apply absolute bg-white w-[470px] my-0 mx-auto transition-all left-[470px] opacity-0;
}

.modal__movable_active {
	@apply left-0 right-0 opacity-100
	max-smFooScr:w-full;
}

.modal__container {
	@apply p-[50px]
	max-smFooScr:p-[25px] max-smFooScr:max-h-[550px];
}

.modal__title_container {
	@apply flex justify-between;
}

.underlined_title__container {
	@apply inline-block;
}

.underlined_title__container span {
	@apply font-medium text-[20px] uppercase tracking-[1.5px] pt-0 pr-[25px] pb-[15px] pl-0 block border-black border-b-[3px]
	max-mobil:mr-[90px];
}

.modal__content {
	@apply mt-[40px];
}

.modal__form_container {
	@apply flex
	max-smFooScr:flex-col;
}

.modal__form {
	@apply w-full;
}

.modal__form_sign_in {
	@apply w-[220px] mt-0 mr-[50px] mb-[50px] ml-0
	max-smFooScr:w-full max-smFooScr:text-center;
}

.modal__form_title {
	@apply text-[11px] font-bold uppercase mb-[30px] tracking-[1.2px]
	max-smFooScr:text-center;
}

.modal__form_input {
	@apply mb-[30px];
}

.custom_input {
	@apply w-full text-[#b3b3b3];
}

.custom_input input {
	@apply border-b-[1px] border-b-[#b3b3b3] text-[16px] outline-none text-black;
}

.modal__socials {
	@apply flex flex-wrap;
}

.modal__social {
	@apply w-1/2 mb-[30px]
max-smFooScr:flex max-smFooScr:items-center max-smFooScr:justify-center;
}

.modal__reset {
	@apply flex items-center justify-center flex-col;
}

.modal__reset button {
	@apply text-[11px] uppercase tracking-[1px];
}
